import React from 'react';
import "./Card.scss"

const Card = ({title, description, icon}) =>
    <div id="Card">
        <div className="cardHeader">
            <img className="cardIcon" src={icon} alt={title}/>

            <h2 id="title">{title}</h2>
        </div>
        <p id="description">{description}</p>
        <div className="orangeBar" />
    </div>;

export default Card;